.proyectos{
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;

    &__rectangle{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    background-color:  $colorSecundary;
    height: 300px;
    border-radius: 10px;
    width: 250px;
    margin-bottom: 30px;
}
&__name{
    font-size: 18px;
}
&__description{
    text-align: center 
    ;
    font-size: 12px;
    padding-top: 10px;
    padding-bottom: 10px;
}
&__list{
    display: flex;
    &--link{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        background-color: white;
        margin-right: 10px;
        border-radius: 5px;
        &:hover{
            background-color: black;
            color: white;
        }
        
    }
}
&__enlace{
    &--link{
    &:hover{
        color: white;
    }
}
}
&__image{
    width: 200px;
    height: 150px;
    padding-top: 10px;
}
&__button{
    background-color: $colorPrincipal;
    margin: auto;
display: flex;
justify-content: center;
align-items: center;
font-size: 12px;
height: 30px;
width: 250px;
border-radius: 5px;
color: white;
font-weight: bold;
margin-bottom: 20px;

}
@include tablet{
    &__rectangle{
        height: 300px;
        width:250px;
        margin-bottom: 50px;
        margin-right: 20px;
        margin-left: 20px;
    }
    &__name{
        font-size: 18px;
    }
    &__description{
        font-size: 14px;

    }
    &__list{
justify-content: center;
        &--link{
            width: 40px;
            height: 40px;
        }
    }
    &__image{
  width:200px;
  height:150px;
  padding-top: 25px;
  margin-bottom: 20px;
}
&__button{
font-size: 18px;
height: 35px;
width: 350px;

}
}
@include desktop{

    &__rectangle{
        height: 310px;
        width: 100%;
    }
    &__name{
        font-size: 18px;
    }
    &__description{
        font-size: 16px;
        width: 250px;

    }
    &__list{

        &--link{
            width: 40px;
            height: 40px;
        }
    }
    &__image{
  width:200px;
  height:150px;
  padding-top: 25px;
  margin-bottom: 20px;
}
&__button{
font-size: 16px;
height: 35px;
width: 300px;

}
}
}

.section-proyects{
    
    @include desktop{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        
    }
}

.projects-list{
    @include tablet{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    @include desktop{
        width: 100%;
        display: grid;
        grid-template-columns: repeat(auto-fill,minmax(16rem, 1fr));
        gap: 40px;
        margin-left: 150px;
        margin-right: 150px;
    }
}


